import * as React from "react";
import Navbar from '../components/navbar';
import Policy from '../components/policy';
import Footer from '../components/footer';
import "/src/css/custom.css";
import { useCookies } from 'react-cookie';

// styles
const pageStyles = {
    color: "#232129",
    fontFamily: "-apple-system, Roboto, sans-serif, serif",
}

// markup
const PolicyPage = () => {

    const [cookies, setCookie] = useCookies(['logo']);

    // ----- Logo -----//
    
    let logo = cookies.logo;
    let rerano = "";
    let disclaimer = "";
    let copyright = "";

    if (typeof window !== 'undefined') {
        //logo = localStorage.getItem('logo');
        rerano = localStorage.getItem('rerano');
        disclaimer = localStorage.getItem('disclaimer');
        copyright = localStorage.getItem('copyright');
    }
    

    return (
        <main style={pageStyles}>
            <title>Prime Park</title>

            <Navbar logo={logo} />
            <Policy />
            <Footer rerano={rerano} disclaimer={disclaimer} copyright={copyright} />

        </main>

    )

}

export default PolicyPage
